

* {
    box-sizing: border-box;
}

li {
    list-style: none;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    margin-top: 40px;
}

.header {
    width: 100%;
    background-color: wheat;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    padding: 8px 10px;
    h1 {
        font-size: 22px;
        text-transform: uppercase;
    }
    nav {
        ul {
            display: flex;
            li {
                font-size: 20px;
                display: flex;
                align-items: center;
                &:nth-child(3n + 1) {
                    span {
                        background-color: blueviolet;
                    }
                }
                &:nth-child(3n + 2) {
                    span {
                        background-color: #56854a;
                    }
                }
                &:nth-child(3n + 3) {
                    span {
                        background-color: #97c2de;
                    }
                }
                span {
                    margin-right: 5px;
                    height: 15px;
                    width: 15px;
                    background-color: red;
                    border-radius: 50%;
                }
                a {
                    color: inherit;
                    text-decoration: none;
                    text-transform: uppercase;
                }
                &:not(:last-child) {
                    margin-left: 20px;
                }
            }
        }
    }
}

.posts {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: 55vh;
    .post {
        padding: 20px;
        .post__header {
            display: flex;
            justify-content: space-between;
        }
        &:nth-child(3n + 1) {
            background-color: blueviolet;
        }
    }
}
